import quickBtn from '@/components/mbbo/quickBtn/quickBtn.vue';
import navigation from '@/components/mbbo/navigation/navigation.vue';
import rQrCode from '@/components/common/RHX/rQrCode/rQrCode';
import rClipBoard from '@/components/common/RHX/rClipBoard/rClipBoard';
import { mapMutations, mapGetters } from 'vuex';
export default {
	data() {
		return {
			list: null,
			tapIndex: 0,
			dataLists:null, // 链接上的参数
			btnList:[
				{
					type: 0,
					tempType: 0,
					name: '我的采购',
					style: {}
				},
				// {
				// 	type: 1,
				// 	tempType: 0,
				// 	name: '复制此采购',
				// 	style: {}
				// },
				{
					type: 2,
					tempType: 0,
					name: '创建新采购',
					style: {
						backgroundImage: 'linear-gradient(134deg, #15D49F 0%, #57D6CF 100%)',
						color:  '#FFFFFF'
					}
				}
			],
			templetType: 0, // 采购摸吧类型 0代表简单模板，1代表普通模板
			moreSet: false, // 运费及更多设置 默认不显示
			previceCaiGou: false, // 判断预览状态， 默认是编辑状态
			listData: {
				serverUrl: "https://img01.iambuyer.com/imgup/upLoad/fileUpload",
				imageData: []
			},
			caiGouList: {
				paraList: [
				],
				requList: [
				],
				imgList: [],
				userId: 760,
				purcTitle: '',
				purcSpec: '',
				purcCount: 1,
				purcUnit: '',
				purcPcode: '',
				purcCcode: '',
				purcCcodeStr: '', // 省市区页面展示
				purcContent: '',
				purcDefault: '0', // 不是默认模板
				purcInvo: '1', // 默认含税
				purcFrei: '1', // 默认含运费
				purcEndTime: '',
				purcReceTime: '0-0-0',
				purcState: '',
				purcType: '0' // 0简单采购 1参数采购
			} // 采购数据
		};
	},
	components: {
		quickBtn,
		navigation,
		rQrCode,
		rClipBoard
	},
	computed: {
		...mapGetters(['GET_PUBLISH', 'QUICKNAVCO'])
	},
	onLoad(option) {
		this.dataLists = this.urlCrypto(option.urlParams, 1);
		console.log(this.dataLists, '----------------------------------------------------')
	},
	beforeDestroy () {
		this.$store.commit('setPreviceCaiGou', false); // 更新setPreviceCaiGou
		this.$store.commit('setMoreSet', false); // 更新setMoreSet
		console.log('页面销毁之前缓存数据更新-----------setImgList、setCaiGouList');
		this.$store.commit('setImgList', this.listData); // 更新ssetImgList
		this.$store.commit('setCaiGouList', this.caiGouList); // 跟新采购列表数据
	},
	created() {
		this.Statistics();
	},
	methods: {
		tapRQrCode (e) {
			console.log(e, '触发二维码组件');
		},
		tapRClicpBoard(e) {
			console.log(e, '剪切板');
		},
		Statistics(){
			if (uni.getStorageSync('landRegist')) {
				let landRegistLG = JSON.parse(uni.getStorageSync('landRegist')); // 读取缓存的用户信息
				console.log(landRegistLG.user.id);
				let params = {}; // 请求总数居时 参数为空
				uni.showLoading({ // 展示loading
					title: '加载中'
				});
				uni.request({
					url: this.api2 + '/rest-rp/putIn/' + landRegistLG.user.id, //接口地址。
					method: 'GET',
					header: {
						Authorization: "Bearer " + landRegistLG.token //将token放到请求头中
					},
					success: (response) => {
						uni.hideLoading();
						console.log(response.data);
						this.list = response.data.content;
					},
					fail: (error) => {
						uni.hideLoading(); // 隐藏 loading
						uni.showToast({
							title: '网络繁忙，请稍后',
							icon: 'none',
							duration: 1000
						});
						console.log(error, '网络繁忙，请稍后');
					}
				});
			}
		},
		goToPutInBusiness() {
			console.log('to投放商机');
			if (this.list.rpCompanyAuth == 0) {
				uni.showModal({
					title: '提示',
					content: '您的公司还未认证，无法进行广告投放，快去提交公司认证信息吧～',
					confirmColor: ' #02C2A2',
					confirmText: '去认证',
					cancelText: '暂不认证',
					success: function(res) {
						if (res.confirm) {
							console.log('用户点击确定');
							let e=1;
							uni.navigateTo({
								url: '/modules/myCompany/personAndCompany/companyInfor/companyInforEdit?id='+e
							});
							
						} else if (res.cancel) {
							console.log('用户点击取消');
						}
					}
				});
			} else {
				if (uni.getStorageSync('landRegist')) {
					let landRegistLG = JSON.parse(uni.getStorageSync('landRegist')); // 读取缓存的用户信息
					console.log(landRegistLG.user.id);
					let params = {
						page: String(1),
						userId: landRegistLG.user.id
					}; // 请求总数居时 参数为空
					uni.showLoading({
						// 展示loading
						title: '加载中'
					});
					uni.request({
						url: this.api2 + '/rest-rp/putIn/selectInstList', //接口地址。
						data: params,
						method: 'POST',
						header: {
							Authorization: 'Bearer ' + landRegistLG.token //将token放到请求头中
						},
						success: response => {
							uni.hideLoading();
							console.log(response.data);
							this.putInBusinessData = response.data.content.list;
							// this.list = response.data.content
							this.$store.commit('setputInBusinessData', this.putInBusinessData); // 更新vuex
							if (response.data.ret == 200) {
								uni.navigateTo({
									url: '/modules/aLittle/putInBusiness/putInBusiness'
								});
							}
						},
						fail: error => {
							uni.hideLoading(); // 隐藏 loading
							uni.showToast({
								title: '网络繁忙，请稍后',
								icon: 'none',
								duration: 1000
							});
							console.log(error, '网络繁忙，请稍后');
						}
					});
				}
			}
		},
		btnItems (e) {
			console.log(e, '按钮');
			if (e.type === 0) {
				console.log('我的采购');
				uni.navigateTo({
					url: '/modules/InstructionsPrice/Purchase/Purchase'
				});
			} else if (e.type === 1) {
				console.log('复制此采购');
				uni.showToast({
					title: '此功能暂未开发，敬请期待！',
					icon: 'none',
					duration: 1000
				});
				// uni.navigateTo({
				// 	url: '/modules/publishPurchase/publishPurchase'
				// });
			} else if (e.type === 2) {
				console.log('创建新采购');
				this.$store.commit('setImgList', this.listData); // 更新ssetImgList
				this.$store.commit('setCaiGouList', this.caiGouList); // 跟新采购列表数据
				if (!uni.getStorageSync('landRegist')) {
					this.landRegistra(); // 判断登录状态
				}
				if (uni.getStorageSync('landRegist')) {
					let lookUserId = null;
					if (uni.getStorageSync('landRegist')) {
						let landRegistLG = JSON.parse(uni.getStorageSync('landRegist')); // 读取缓存的用户信息
						console.log(landRegistLG.user.id);
						lookUserId = landRegistLG.user.id;
					}
					let urlParams = {
						isFlow: 0, // 平台来源
						orderType: 0, // 订单类型
						sourceTemp: 0, // 说明书来源
						aiListTemp: 0, // 是否展示ai
						purcId:  null, // 说明书id
						lookUserId: '', // 浏览者
						creatorId: '', // 说明书创建者
						purcState: '' // 说明书状态
					};
					uni.navigateTo({
						url: '/modules/publishPurchase/publishPurchase?urlParams=' + this.urlCrypto(urlParams, 0)
					});				
				}
			}
		}
	},
};
